/* eslint-disable no-unused-vars */
export enum RequestMethod {
  GET = 'GET',
  POST = 'POST',
  PUT = 'PUT',
  DELETE = 'DELETE',
  PATCH = 'PATCH',
  HEAD = 'HEAD',
  COMMON = 'COMMON',
}

export enum ResponseType {
  arraybuffer = 'arraybuffer',
  blob = 'blob',
  json = 'json',
  text = 'text',
  formData = 'formData',
}

export interface FetchConfig extends RequestInit {
  /**
   * @description `baseURL`  `url`  `url`  URL
   * @description  `baseURL`  axios  URL
   */
  baseURL?: string

  /**
   * @description `url`  URL
   */
  url?: string

  /**
   * @description `method` , get
   */
  method?: RequestMethod

  /**
   * @description `transformRequest` 
   * @description  'PUT', 'POST'  'PATCH' 
   * @description  ArrayBuffer Stream
   */
  transformRequest?: Function[]

  /**
   * @description `transformResponse`  then/catch 
   * @description  'PUT', 'POST'  'PATCH' 
   * @description  ArrayBuffer Stream
   */
  transformResponse?: Function[]

  /**
   * @description `params`  URL 
   * @description (plain object) 
   */
  params?: {
    [key: string]: any
  }

  /**
   * @description `timeout` (0 )
   * @description  `timeout` 
   * @default timeout = 1000 * 5
   */
  timeout?: number

  /**
   * @description `responseType`  'arraybuffer', 'blob', 'document', 'json', 'text', 'stream'
   */
  responseType?: ResponseType

  /**
   * @description `xsrfCookieName`  xsrf token cookie
   */
  xsrfCookieName?: string

  /**
   * @description `xsrfHeaderName`  xsrf token  HTTP 
   */
  xsrfHeaderName?: string

  /**
   * @description `onUploadProgress` 
   */
  onUploadProgress?: Function

  /**
   * @description `onDownloadProgress` 
   */
  onDownloadProgress?: Function

  /**
   * @description `maxContentLength` 
   */
  maxContentLength?: number

  /**
   * @description `validateStatus` HTTP  resolve  reject  promise  `validateStatus`  `true` ( `null`  `undefined`)promise  resolve; promise  rejecte
   */
  validateStatus?: Function

  /**
   * @description `maxRedirects`  node.js  follow 
   * @description 0 follow 
   */
  maxRedirects?: number
}

export type Handlers = {
  fulfilled: Function
  rejected: Function
} | null
